/* eslint-disable react-hooks/exhaustive-deps */
import type { BiPluginConfig } from '@ali/4ever-cangjie';
import React from 'react';
import DragDrowdown from './dargDropdown';

export default (config: BiPluginConfig) => {
  return ({ controller, currentBlock, scale = 1 }) => {
    const { composing } = controller.value;
    const isNewLine = controller.query('isInsertBtnDisplay', {
      node: currentBlock,
      composing,
    });
    const isHideDragButton = controller.query('isHideDragButton', { node: currentBlock });
    const nodeText = currentBlock?.text;
    const showBtn = (nodeText || !isNewLine) && !isHideDragButton;
    if (showBtn) {
      return (
        <DragDrowdown
          controller={controller}
          currentBlock={currentBlock}
          draggableConfig={config.draggable}
          disableDrag={!config.draggable?.enabled}
          scale={scale}
        />
      );
    }
    return null;
  };
};
